.card-list {
  display: flex;
  flex-direction: column;
  background: #f5f6f7;
}
.card-list {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0 104px;
}
